<template>
  <div class="back-black" v-if="modalOpen == true">
    <div class="back-white">
      <h4>Artist note</h4>
      <p>
        {{ photosData_[choice].note }}
      </p>
      <button @click="modalOpen = false" type="button" class="btn btn-secondary">close</button>
    </div>
  </div>

<Nav />

  <div class="mb-3">
    <button @click="sortDate" type="button" class="btn btn-secondary me-3">
      NEWEST
    </button>
    <button @click="sortId" type="button" class="btn btn-secondary">
      CANCEL
    </button>
  </div>

  <div v-for="(photosData__, i) in photosData_" :key="i">
    <div class="mt-5">
      <img
        :src="photosData_[i].img"
        style="width: 100%"
      />
      <p class="text-center mt-2">{{ photosData__.name }}</p>
      <p class="text-center mt-2">{{ photosData__.date }}</p>
      <button @click="modalOpen = true; choice = i;" type="button" class="btn btn-secondary">
        Artist note
      </button>
    </div>
  </div>

  <Footer />


</template>

<script>
import Nav from "./components/Nav.vue";
import Footer from "./components/Footer.vue";
import photosData from "./data/photosData";

console.log(photosData[1].name)


export default {
  name: "App",
  data(){
    return{
      photosData_: photosData,
      photosDataOri: [...photosData],
      modalOpen: false,
      choice: 0,
    }
  },
  methods: {
    sortDate(){
      this.photosData_.sort(function (a, b) {
        return b.date - a.date;
      })
    },
    sortId() {
      this.photosData_ = [...this.photosDataOri]
    },
  },
  components: {
    Nav,
    Footer,
  },
};
</script>

